/**
 * App.js
 *
 * This component is the skeleton around the actual pages, and
 * contains code seen on all pages.
 */
import React from 'react';
import { Switch, Route, HashRouter as Router } from 'react-router-dom';
 
 // Content
import HomePage from './pages/HomePage/Loadable';
import Results from './pages/Results/Loadable';
import Header from './components/Header';

// Style
import './App.css';
import GlobalStyle from './global-styles';

function App() {
  // Initialize state with default values of parameters
  const [state, setState] = React.useState({
    blocked: true,
    token: '',
  });

  const handleTokenChange = (event) => {
    setState({...state, token: event.target.value});
  };

  // Evaluate access token
  const handleEvaluation = (event) => {
    event.preventDefault();
    if (state.token === 'lolo') {
      setState({...state, blocked: false});
    }
    else {
      setState({...state, token: ''});
    }
  };

  return (
    <Router>
      {state.blocked ? (
        <div className="Login">
          <b>This page is protected. Please enter an access token:</b><br /><br />
          <form onSubmit={handleEvaluation}>
            <label>
              Secret Token:{' '}
              <input type="password" name="token" value={state.token} onChange={handleTokenChange} />
            </label>{' '}
            <input type="submit" value="Unlock" />
          </form>
        </div>
      ) : (
        <div>
          <Header />
          <Switch>
            <Route exact path="/" component={HomePage} />
            <Route exact path="/results" component={Results} />
          </Switch>
          <GlobalStyle />
        </div>
      )}
    </Router>
  );
}

export default App;
